import { Box, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles({
    root: {
        border: '1px solid grey',
        display: 'flex',
        flexDirection: 'column',
        alignContent: 'center',
        alignItems: 'stretch',
        justifyContent: 'center',
        padding: '5px',
        minWidth: '50px',
        minHeight: '20px',
        textAlign: 'center'
    },
  });

export default function Core({clock, load, power}) {
    const classes = useStyles();
    return(<Box className={classes.root}>
        <div><Typography variant="caption">{clock.toFixed(0)} MHz</Typography></div>
        <div><Typography variant="caption">{power.toFixed(0)} Watts</Typography></div>
        <div><Typography variant="caption">{load.toFixed(0)}%</Typography></div>

    </Box>)
}