import React from 'react';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Temp from './base-components/temp'
import Usage from './base-components/usage'
import CPUGraphic from './cpu-components/cpu-graphic'
import LoadChart from './base-components/load-chart';
import MemProgress from './base-components/progress'

export default function CPU({ name, temp, load, clock, power, cores, loadHistory, memUsed, memAvail, ramLoad }) {
    return (<div className="statsContainer">
        <Typography variant="h5" component="div">CPU</Typography>
        <Typography variant="h5" component="div">{name}</Typography>
        <Typography variant="body1">Clock: {(clock / 1000).toFixed(2)} GHz</Typography>
        <Typography variant="body1">Power: {power.toFixed(0)} Watts</Typography>
        {/* <div>
            <div className="chartContainer"><CPUGraphic cores={cores} /></div>
        </div> */}

        <div style={{ width: '80%' }}>
            <Typography variant="h6">RAM</Typography>
            <MemProgress variant="determinate" value={ramLoad} caption={`${(memUsed.toFixed(2))}/${(memUsed + memAvail).toFixed(2)} GB (${Math.round(ramLoad)}%)`} />
        </div>
        <Box marginTop="auto" display="flex">
            <div className="chartContainer">
                {/* <Usage
                    variant="determinate"
                    value={load}
                    size={125}
                /> */}
                <LoadChart data={loadHistory} load={load} />
            </div>
            <div className="chartContainer">
                <Temp
                    variant="determinate"
                    value={temp}
                    size={125}
                />
            </div>

        </Box>

    </div>)
}