import React from 'react';
import Paper from '@material-ui/core/Paper';
import {
    ArgumentAxis,
    ValueAxis,
    Chart,
    AreaSeries,
} from '@devexpress/dx-react-chart-material-ui';
import { ValueScale } from '@devexpress/dx-react-chart';
import { Box, Typography } from '@material-ui/core';
import { green, yellow, red, purple } from '@material-ui/core/colors';

const modifyDomain = ([start, end]) => [0, 100];

function getColor(val) {
    if (val < 70) return green[300]
    else if (val >= 70 && val < 90) return yellow[300]
    else if (val >= 90) return red[300]
}

export default function LoadChart({ data, load }) {
    return (<Paper component="div">
        <Box display="flex" flexDirection="row" width="100%" justifyContent="end" alignContent="right" marginLeft="4px" paddingRight="20px" >
            <Typography variant="h6" display="inline">Load</Typography>
            <Box marginLeft="auto" color={getColor(load)}><Typography variant="h4" display="inline" >{load.toFixed(0)}%</Typography></Box>
        </Box>
        <Chart
            data={data}
            height={100}
        >
            <ValueScale modifyDomain={modifyDomain} />
            <ArgumentAxis showLabels={false} showGrid={false} showTicks={false} />

            <ValueAxis showLabels={false} showLine={false} showGrid={false} decimal={false} />

            <AreaSeries color={purple[300]} valueField="load" argumentField="t" />
        </Chart>
    </Paper>)
}