import React, { useState, useEffect, useRef } from 'react';
import socketIOClient from "socket.io-client";

import CPU from './cpu';
import GPU from './gpu';
import RAM from './ram';
import HDD from './hdd'
import Nicehash from './nicehash'

const ENDPOINT = "http://localhost:23465";

export default function Main() {
    const [data, setData] = useState([])
    const [cpuLoadHistory, setCpuLoadHistory] = useState([])
    const [gpuLoadHistory, setGpuLoadHistory] = useState([])
    const [nicehashData, setNicehashData] = useState()

    useEffect(() => {
        const socket = socketIOClient(ENDPOINT);
        socket.on("connect", () => {
            console.log('connected')
        })

        socket.on('nicehash', (d) => {
            setNicehashData(d)
        })

        socket.on("stats", (d) => {
            setData(d)
            saveLoads(d)
        })

    }, []);

    const saveLoads = (data) => {
        if (data) {
            var cpu = data.find(hw => hw.hardwareType === 'CPU')
            if (cpu) {
                var cpuLoad = cpu.sensors.Load['CPU Total']
                var newVal =  { t: new Date(), load: cpuLoad }
                setCpuLoadHistory(state => [...state, newVal].splice(-60, 60))
            }


            var gpu = data.find(hw => hw.hardwareType === 'GpuNvidia')
            if (gpu) {
                var gpuLoad = gpu.sensors.Load['GPU Core']
                var newVal =  { t: new Date(), load: gpuLoad }
                setGpuLoadHistory(state => [...state, newVal].splice(-60, 60))
            }
        }
    }

    var objs = []

    for (let hw of data) {
        switch (hw.hardwareType) {
            case 'CPU':
                var cores = {}
                var name = hw.report.find(r => r.includes('Name:')).split('Name: ')[1]
                var temp = hw.sensors.Temperature['CPU Package']
                var load = hw.sensors.Load['CPU Total']
                var clock = hw.sensors.Clock['CPU Core #1']
                var power = hw.sensors.Power['CPU Package']
                var ram = data.find(d => d.hardwareType === 'RAM')
                if (ram) {
                    var memUsed = ram.sensors.Data['Used Memory']
                    var memAvail = ram.sensors.Data['Available Memory']
                    var ramLoad = ram.sensors.Load['Memory']
                }
                for (let i in hw.sensors.Load) {
                    if (i.includes("CPU Core #")) {
                        cores[i.split("CPU Core #")[1]] = {
                            power: hw.sensors.Load[i],
                            load: hw.sensors.Power[i],
                            clock: hw.sensors.Clock[i],
                        }
                    }
                }
                objs.push(<CPU name={name} temp={temp} load={load} clock={clock} power={power} cores={cores} loadHistory={cpuLoadHistory} memUsed={memUsed} memAvail={memAvail} ramLoad={ramLoad} v/>)
                break;
            case 'GpuNvidia':
                var name = hw.report.find(r => r.includes('Name:')).split('Name: ')[1]
                var temp = hw.sensors.Temperature['GPU Core']
                var load = hw.sensors.Load['GPU Core']
                var coreClock = hw.sensors.Clock['GPU Core']
                var memClock = hw.sensors.Clock['GPU Memory']
                var memTotal = hw.sensors.SmallData['GPU Memory Total']
                var memUsed = hw.sensors.SmallData['GPU Memory Used']
                objs.push(<GPU name={name} temp={temp} load={load} coreClock={coreClock} memClock={memClock} memUsed={memUsed} memTotal={memTotal} loadHistory={gpuLoadHistory} />)
                break;
            // case 'RAM': 
            //     var memUsed = hw.sensors.Data['Used Memory']
            //     var memAvail = hw.sensors.Data['Available Memory']
            //     var load = hw.sensors.Load['Memory']
            //     objs.push(<RAM memUsed={memUsed} memAvail={memAvail} load={load} />)
            //     break;
            // case 'HDD':
            //     if (!hw.sensors.Load) break;
            //     var id = hw.id.split('/hdd/')[1]
            //     var load = hw.sensors.Load['Used Space']
            //     var temp = hw.sensors.Temperature ? hw.sensors.Temperature.Temperature : null
            //     objs.push(<HDD id={id} load={load} temp={temp}/>)
            //     break;
            default:
                break;
        }
    }
    if (nicehashData) objs.push(<Nicehash data={nicehashData} />)

    return (<div className="allContainer">
        {data ? objs : 'No data'}
    </div>)
}