import React from 'react';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import MemProgress from './base-components/progress'
import Temp from './base-components/temp'
import LoadChart from './base-components/load-chart'
import Usage from './base-components/usage'

export default function GPU({ name, temp, load, coreClock, memClock, memUsed, memTotal, loadHistory }) {
    return (<div className="statsContainer">
        <div><Typography variant="h5">GPU</Typography></div>
        <div><Typography variant="h5">{name}</Typography></div>
        <div><Typography variant="body1">Core: {coreClock.toFixed(0)} MHz</Typography></div>
        <div><Typography variant="body1">Mem: {memClock.toFixed(0)} MHz</Typography></div>
        <div style={{ width: '80%' }}>
            <Typography variant="h6">VRAM</Typography>
            <MemProgress variant="determinate" value={memUsed / memTotal * 100} caption={`${(memUsed / 1000).toFixed(2)}/${(memTotal / 1000).toFixed(2)} GB (${Math.round(memUsed / memTotal * 100)}%)`} />
        </div>

        <Box marginTop="auto" display="flex">
            <div className="chartContainer">
                {/* <Usage
                    variant="determinate"
                    value={load}
                    size={125}
                /> */}
                <LoadChart data={loadHistory} load={load} />
            </div>
            <div className="chartContainer">
                <Temp
                    variant="determinate"
                    value={temp}
                    size={125}
                />
            </div>


        </Box>

    </div>)
}