import React from 'react';
import CircularProgress from '@material-ui/core/CircularProgress';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import { green, yellow, red, grey } from '@material-ui/core/colors';
function getColor(val) {
    if (val < 65) return green[300]
    else if (val >= 65 && val < 85) return yellow[300]
    else if (val >= 85) return red[300]
}

export default function Temp(props) {
    return (
        <Box position="relative" display="inline-flex">
            <div style={{ transform: 'rotate(-180deg)' }}>
                <CircularProgress variant="determinate" {...props} value={100} style={{ color: grey[700], position: 'absolute' }} />
                <CircularProgress variant="determinate" {...props} style={{ color: getColor(props.value) }} />
            </div>
            <Box
                top={0}
                left={0}
                bottom={0}
                right={0}
                position="absolute"
                display="flex"
                alignItems="center"
                justifyContent="center"
                flexDirection="column"
            >
                <Typography variant="h6" component="div" color="textPrimary">Temp</Typography>
                <Typography variant="h4" component="div" style={{color: getColor(props.value)}}>{Math.round(props.value)}Â°C</Typography>
            </Box>
        </Box>
    );
}