import { Typography, Box, TableContainer, TableHead, TableRow, TableCell, TableBody, Table, Paper } from '@material-ui/core';
import Moment from 'react-moment';

export default function Nicehash({ data }) {
    return (<div className="statsContainer" style={{ flex: 1 }}>
        <Typography variant="h5">Mining Stats</Typography>
        {/* <Typography variant="h6">Wallet Address: {data.btcAddress}</Typography> */}
        {data ? <Box textAlign="center">
            <Typography variant="body1">
                As of <Moment format='YYYY-MM-DD hh:mm:ss'>{data.ts}</Moment>
            </Typography>
            <Typography variant="h6">
                Profitability/24 hrs: {data.totalProfitability.toFixed(8)}BTC (${(data.totalProfitability * data.prices.BTCUSDC).toFixed(2)})
            </Typography>
            <Typography variant="h6">
                Unpaid: {parseFloat(data.unpaidAmount).toFixed(8)}BTC (${(parseFloat(data.unpaidAmount) * data.prices.BTCUSDC).toFixed(2)})
            </Typography>
            {/* <TableContainer component={Paper}>
                <Table size="small" aria-label="a dense table">
                    <TableHead>
                        <TableRow>
                            <TableCell>Name</TableCell>
                            <TableCell align="right">Algorithm</TableCell>
                            <TableCell align="right">Speed</TableCell>
                            <TableCell align="right">Profitability</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {data.miningRigs ? data.miningRigs.map((r) => (
                            <TableRow key={r.name}>
                                <TableCell component="th" scope="row">{r.name}</TableCell>
                                <TableCell align="right">{r.stats[0].algorithm.description}</TableCell>
                                <TableCell align="right">{r.stats[0].speedAccepted.toFixed(2)} MH/s</TableCell>
                                <TableCell align="right">{r.profitability.toFixed(10)} BTC/24 hours</TableCell>
                            </TableRow>
                        )) : null}
                    </TableBody>
                </Table>
            </TableContainer> */}
        </Box> : null}
    </div>)
}